function tabs() {
  const parentTab = document.querySelector(".tabheader__items"),
    tabs = document.querySelectorAll(".tabheader__item"),
    tabsContent = document.querySelectorAll(".tabcontent");

  function hideTabContent() {
    tabsContent.forEach((item) => {
      item.classList.add("hide");
      item.classList.remove("show", "fade");
    });
    tabs.forEach((item) => {
      item.classList.remove("tabheader__item_active");
    });
  }
  function showTabContent(i = 0) {
    tabs[i].classList.add("tabheader__item_active");
    tabsContent[i].classList.add("show", "fade");
    tabsContent[i].classList.remove("hide");
  }
  hideTabContent();
  showTabContent();
  //if we click on parent div(tabs item div) where element has correct class list, itterate throw tabs where target equel element and do next functions
  parentTab.addEventListener("click", (e) => {
    if (e.target && e.target.classList.contains("tabheader__item")) {
      tabs.forEach((item, i) => {
        if (e.target === item) {
          hideTabContent();
          showTabContent(i);
        }
      });
    }
  });
}

module.exports = tabs;
